import React,{useContext} from "react";
import "../css/BookItems.css";
import Items from "./Items";
import itemContext from "../Context/items/ItemContext";

const BookItems = () => {
  const a= useContext(itemContext);
  return (
    <>
      <section id="specialoffers">
      <div className="container" >
      
        <h1>Special Offers - Grave It Now</h1>
        <div className="row row1 ">
        {a.map((a)=>{
         return(
          
              <div className="col-md-3  " >
                <div className="container-fluid">
              <Items key="name" name={a.name} salts={`${a.salts}`} price={a.price} image_url={a.image_url}/>
              </div>
              </div>       
         )
        })}
        
        </div>    
      </div>
      </section>
      <section id="BookItems" className="my-5">
      <h1>Book Your Order Here </h1>
      <div className="container" >
        <div className="row">
          <div className="col-md-6">
          <form>
                <div className="mb-3">
                  <label htmlFor="InputEmail1" className="form-label">Email address</label>
                  <input type="email" className="form-control" id="InputEmail1" aria-describedby="emailHelp"/>
            
                </div>
                <div className="mb-3">
                  <label htmlFor="phone_number" className="form-label">Phone Number</label>
                  <input type="text" className="form-control" id="phone_number"/>
                </div>
                <div className="mb-3">
                <label htmlFor="Textarea">Enter Your Addsress</label>
                <textarea className="form-control" placeholder="Enter Your Address Here" id="Textarea"></textarea> 
              </div>
              <div className="mb-3">
                <label htmlFor="floatingTextarea">Enter Good Name Which You Want To Buy</label>
                <textarea className="form-control" placeholder="Medicine Name Here" id="floatingTextarea" style={{height:"280px"}}></textarea> 
              </div>
                <button type="submit" className="btn btn-primary"style={{marginBottom:"25px"}} >Confirm Your Order</button>
          </form>
          </div>
          <div className="col-md-6">
            <form>
            <div className="mb-3 my-4">
                <label htmlFor="medicinecart"><h1 style={{fontSize:"30px"}}>Medicines You Want To Add In Cart</h1></label>
                <textarea className="form-control" placeholder="Medicines You Want To Buy Displayed Here" id="medicinecart" style={{height:"500px"}}></textarea> 
              </div>
            </form>
          </div>
        </div>
      </div>
      </section>
      
    </>
  );
};

export default BookItems;
