import React from 'react'

// Import Swiper React components
import { Swiper, SwiperSlide } from 'swiper/react';

// Import Swiper styles
import 'swiper/css';
import 'swiper/css/effect-coverflow';
import 'swiper/css/pagination';



// import required modules
import { EffectCoverflow, Pagination } from 'swiper/modules';
import '../css/bodySlider.css'

const BodySlider = () => {
  return (
    <>
    <div className="container ">
    <Swiper
        effect={'coverflow'}
        grabCursor={true}
        centeredSlides={true}
        slidesPerView={'auto'}
        coverflowEffect={{
          rotate: 50,
          stretch: 0,
          depth: 100,
          modifier: 1,
          slideShadows: true,
        }}
        pagination={true}
        modules={[EffectCoverflow, Pagination]}
        className="mySwiper"
      >
        <SwiperSlide><img src="https://source.unsplash.com/iIDiRMITYik/300x300" alt="" /></SwiperSlide>
        <SwiperSlide><img src="https://source.unsplash.com/vCldoZQUqsM/300x300" alt="" /></SwiperSlide>
        <SwiperSlide><img src="https://source.unsplash.com/dHZ5F9HO9R0/300x300" alt="" /></SwiperSlide>
        <SwiperSlide><img src="https://source.unsplash.com/S1v7hVUiCg0/300x300" alt="" /></SwiperSlide>
        <SwiperSlide><img src="https://source.unsplash.com/4-npFikIDGs/300x300" alt="" /></SwiperSlide>
        <SwiperSlide><img src="https://source.unsplash.com/MaKsx8JNbiI/300x300" alt="" /></SwiperSlide>
        <SwiperSlide><img src="https://source.unsplash.com/green-pills-are-arranged-on-a-black-background-lyNuVCHmAS8/300x300" alt="" /></SwiperSlide>
        <SwiperSlide><img src="https://source.unsplash.com/Axq-vcHtC-s/300x300" alt="" /></SwiperSlide>
        <SwiperSlide><img src="https://source.unsplash.com/FEPfs43yiPE/300x300" alt="" /></SwiperSlide>
      </Swiper>
      {/* <!-- Add Pagination -->
       <div class="swiper-pagination swiper-pagination1"></div> */}
      </div>
    </>
  )
}

export default BodySlider
